<script>
import Vue from 'vue'
import vSelect from 'vue-select'
import BootstrapVue from 'bootstrap-vue'
import domtoimage from 'dom-to-image';
import FileSaver from 'file-saver';

Vue.use(BootstrapVue)
Vue.component('v-select', vSelect)

Vue.config.productionTip = false


export default {
  name: 'HelloWorld',
  props: {
    msg: String,
    icon2_check: Boolean
  },
  methods: {
    set_icon(){
        let icon_tag = document.querySelector('#content-icons i') 
        let initial_style_attr = icon_tag.getAttribute("style")
        icon_tag.setAttribute('style', initial_style_attr + ' font-size:'+this.icon_tag.size+'px;' + ' transform:'+'rotate('+this.icon_tag.angle+'deg);')
    },
    check_icon(){
        let icon_tag = document.querySelector('#content-icons i') 

        icon_tag.style.fontSize = this.icon_tag.size + 'px'
        icon_tag.style.transform = 'rotate('+this.icon_tag.angle+'deg)'
    },
    normalize_file_name(name){
        return name.replaceAll(' ', '_').replaceAll('-', '_')
    },
    capture_img: function(){
        var filaname_prefix = "imgen_"
        var filename = filaname_prefix+this.normalize_file_name(this.icon.fa_class)+'.png'
        domtoimage.toBlob(document.getElementById('capture'))
            .then(function (blob) {
                FileSaver.saveAs(blob, filename);
            })
            .catch(function (error) {
                console.error('oops, something went wrong!', error);
            });
    },
    restore_saved_color: function(event, icon){
        if(icon == 1){
            this.icon.color = event.currentTarget.style.backgroundColor
        }else{
            this.icon2.color = event.currentTarget.style.backgroundColor
        }
        
    },
    restore_saved_color_frame: function(event){
        this.frame.color = event.currentTarget.style.backgroundColor
    },
    optionsFilter(options, search) {
      options = []
      if(search.length>=3){
            options = this.fa_icons.filter(word => word.indexOf(search) != -1);
      }
      return options
    }
  },
